import React,{useState} from 'react'
import {Typography,Grid,Paper,Avatar,Button,Container} from '@material-ui/core'
import {GoogleLogin} from 'react-google-login'
import {useHistory} from 'react-router-dom'
import Icon from './icon'
import {useDispatch} from 'react-redux'
import LockOutlinedIcon from '@material-ui/icons/LockOpenOutlined'
import useStyles from './style'
import Input from './Input'
import {signIn , signUp} from '../../actions/auth'
const Auth = () => {
    const state = null
    const classes = useStyles()
    const dispatch = useDispatch()
    const history = useHistory()
    const initialState = {firstName:'',lastName:'',email:'',password:'',confirmPassword:''}
    const [showPassword,setShowPassword] = useState(false)
    const [isSignup , setIsSignup] = useState(false)
    const [formData,setFormData]= useState(initialState)
    const handleSubmit=(e)=>{
        e.preventDefault()
        // console.log(formData)
        if(isSignup)
        {
            dispatch(signUp(formData,history))

        }
        else
        {
            dispatch(signIn(formData,history))
        }
    }
    const handleChange=(e)=>{
        setFormData({...formData,[e.target.name]:e.target.value})
        


    }
    const handleShowPassword = ()=>setShowPassword((prevShowPassword)=>!prevShowPassword)
    const switchMode = ()=>{
        setIsSignup((previsSignup)=>!previsSignup)
        setShowPassword(false)
    }
    const googleSuccess=(res)=>{
        const result = res?.profileObj;//?. is a special character which doesn't give error  if we don't have access to res object
        const token = res?.tokenId;

        try 
        {
            dispatch({type:'AUTH' , data:{result,token}})
            history.push('/')
        } 
        catch (error) 
        {
            console.log(error)
        }

        console.log(res)
    }
    const googleFailure=(error)=>{
        console.log("Google Sign In was unsuccessful.Try again later")
        console.log(error)
    }
    return (
        
        <Container component="main" maxWidth="xs">
            <Paper className={classes.paper} elevation={3}>
                <Avatar className={classes.avatar}>
                    <LockOutlinedIcon/>
                </Avatar>
                <Typography variant="h5">{isSignup?'SignUp':'SignIn'}</Typography>
                <form className={classes.form} onSubmit={handleSubmit}>
                    <Grid container spacing={2}>
                        {
                            isSignup && (
                            <>
                                <Input name ="firstName" label="First Name" handleChange={handleChange} autofocus half/>
                                <Input name ="lastName" label="Last Name" handleChange={handleChange} half/>
                               
                            </>
                            )}
                            <Input name ="email" label="Email Address" handleChange={handleChange} type="email"/>
                            <Input name="password" label="Password" handleChange={handleChange} type={showPassword?'text':'password'} handleShowPassword={handleShowPassword}/>
                            {isSignup && <Input name="confirmPassword" label="Repeat Password" type="password" handleChange={handleChange}/>}
                    </Grid>
                    
                    <Button type="submit" fullWidth variant="contained" color="primary" className={classes.submit}>
                        {isSignup?'Sign Up' : 'Sign In'}
                    </Button>
                    <GoogleLogin
                        clientId="395242683556-2ehthi13nbg2r80e3h79q1c0cap0egeb.apps.googleusercontent.com"
                        render={(renderProps)=>(
                            <Button 
                            className={classes.googleButton} 
                            color='primary' 
                            fullWidth 
                            onClick={renderProps.onClick} 
                            disabled={renderProps.disabled} 
                            startIcon={<Icon/>}  
                            variant="contained">
                                Google Sign In
                            </Button>
                        )}
                        onSuccess={googleSuccess}
                        onFailure={googleFailure}
                        cookiePolicy="single_host_origin"
                    />
                    <Grid container justify="flex-end">
                        <Grid item>
                            <Button onClick={switchMode}>
                                {isSignup?"Already have an account? Sign In":"Don't have an account? Sign Up"}
                            </Button>
                        </Grid>

                    </Grid>

                </form>

            </Paper>
        </Container>  
      
    )
}

export default Auth
